import { Link } from 'react-router-dom';

function NotFound() {
  return (
    <>
      <h1>404</h1>
      <div>Looks like youve got lost...</div>
      <div>The page youre looking for doesnt exist or has been moved.</div>
      <Link to="/">Go Home</Link>
    </>
  );
}

export default NotFound;
